.todoWrapper {
    border-bottom: 1px solid #efefef;
    text-align: left;
    margin-bottom: 10px;
    margin-top: 30px;
    color: white;
  }

  .removeTodo {
    margin-right: 20px;
    margin-bottom: 10px;
    outline: none;
    box-shadow: none;
    border-width: 2px;
    border-radius: 3px;
    border-style: solid;
    padding: 8px;
    color: rgba(203, 20, 32, 0.4);
    border-color: rgba(203, 20, 32, 0.4);
    background-color: transparent;
  }

  .removeTodo:hover {
    background-color: rgba(203, 20, 32, 0.4);
    color: white;
    border-color: white;
  }